html {
  box-sizing: border-box;
}

@font-face {
  font-family: "Sansation Bold Italic";
  src: url("./css/Sansation_Bold_Italic.ttf");
}
@font-face {
  font-family: "Sansation_Light";
  src: url("./css/Sansation_Light.ttf");
}
/* , *:before, *:after{
    box-sizing: inherit;
} */
body {
  font-family: "Exo", sans-serif;
  /*font-family: 'Montserrat', sans-serif;*/
  font-size: 14px;
  font-weight: 300px;
  background: #e2e2e2;
  margin: 0;
  padding: 0;
}

.container {
  display: grid;
  /*
    grid-template-columns:  auto 970px auto;
    grid-template-rows: 4fr 5fr 4fr 6fr 3fr 1fr;
    */
  width: 100%;
  max-width: 75rem;
  grid-gap: 3px;
  margin: auto;
}
/*
.container > div {
